.burger {
  @extend %p-rel;
  @extend %d-flex-al-ctr;
  @extend %pointer;
  height: rem(15px);
  width: rem(20px);

  div {

    &,
    &::before,
    &::after {
      @extend %w-100;
      @extend %p-abs;
      @extend %tr-pr;
      background-color: $clr-icon;
      height: rem(3px);
    }

    &::before,
    &::after {
      @extend %content;
    }

    &::before {
      top: 6px;
    }

    &::after {
      bottom: 6px;
    }
  }

  &.is-close {
    div {
      right: rem(5px);
      top: rem(3px);
      transform: rotate(45deg);
      width: 50%;

      &::before {
        opacity: 0;
      }

      &::after {
        right: rem(-4px);
        top: rem(4px);
        transform: rotate(90deg);
      }
    }
  }

  &:hover {
    div {

      &,
      &::before,
      &::after {
        background-color: $clr-bg-side-hdr;
      }
    }
  }
}
